@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&family=Roboto:ital@1&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 15px;
}

.menu_btn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.menu_btn div,
.menu_btn div::before,
.menu_btn div::after {
  background: #1a1a1a;
  border-radius: 10px;
  width: 40px;
  height: 5px;
  transition: all 0.2s linear;
}

.menu_btn div,
.menu_btn div::before,
.menu_btn div::after {
  content: "";
  position: absolute;
}

.menu_btn div::before {
  transform: translateY(-200%);
}

.menu_btn div::after {
  transform: translateY(200%);
}

.menu_btn.open div {
  background: transparent;
}

.menu_btn.open div::before {
  transform: rotate(-45deg);
}

.menu_btn.open div::after {
  transform: rotate(45deg);
}

.title {
  text-align: center;
  font-size: 45px;
  margin: 25px 0;
  color: #1a1a1a;
}

.top_nav {
  background: #d7d7e2;
}

.top_nav .container {
  display: flex;
  min-height: 60px;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.top_nav .top_nav__block {
  display: flex;
}

.top_nav .top_nav__block a {
  display: block;
  text-decoration: none;
  margin-right: 15px;
}

.top_nav .top_nav__block a svg {
  width: 45px;
  height: 45px;
  fill: #e3774c;
  stroke: rgb(244, 255, 251);
  stroke-width: 1.5px;
}

.top_nav .top_nav__block a:last-child {
  margin-right: 0;
}

.top_nav .top_nav__block a {
  font-weight: bold;
}

nav {
  position: sticky;
  background: #fff;
  top: 0;
  left: 0;
  z-index: 99999;
  box-shadow: 0 6px 36px rgba(0, 0, 0, 0.09);
}

nav .container {
  display: flex;
  justify-content: space-between;
  min-height: 80px;
  align-items: center;
  position: relative;
}

nav .container .nav_menu .menu_btn {
  display: none;
}

nav .container .nav_menu ul {
  display: flex;
  justify-content: space-between;
}

nav .container .nav_menu ul li {
  list-style-type: none;
}

nav .container .nav_menu ul li a {
  width: 100%;
  display: flex;
  height: 80px;
  align-items: center;
  padding: 8px;
  color: #1a1a1a;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
  letter-spacing: 1px;
}

nav .container .nav_menu ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  color: #fff;
  background: #e3774c;
  transition: height 0.4s;
  z-index: -1;
}

nav .container .nav_menu ul li a:hover {
  color: #fff;
}

nav .container .nav_menu ul li a:hover::after {
  height: 100%;
}

