header {
  display: flex;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  background-attachment: fixed;
  position: relative;
  height: 90vh;
  overflow: hidden;
}

header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(48, 13, 1, 0.6862745098);
}

header .header_content {
  display: flex;
  flex-flow: column;
  align-content: center;
  align-items: center;
  position: relative;
  color: #fff;
  z-index: 55;
  text-align: center;
  width: 100%;
}

header .header_content h1 {
  text-transform: uppercase;
  font-size: 60px;
  letter-spacing: 2px;
}

header .header_content p {
  line-height: 30px;
  margin-bottom: 20px;
  max-width: 600px;
}

header .header_content .header_content__buttons {
  display: flex;
}

header .header_content .header_content__buttons a,
header .header_content .header_content__buttons button {
  display: block;
  padding: 15px 40px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
}

header .header_content .header_content__buttons a {
  color: #fff;
  background: #e3774c;
  text-decoration: none;
  margin-right: 20px;
  transition: background 0.3s;
}

header .header_content .header_content__buttons a:hover {
  background: #f55d22;
}

header .header_content .header_content__buttons button {
  color: #fff;
  border: 2px solid #fff;
  background: transparent;
  font-size: 16px;
}

header .header_content .header_content__buttons button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: rgba(255, 255, 255, 0.288);
  transition: width 0.15s;
  z-index: -1;
}

header .header_content .header_content__buttons button:hover::before {
  width: 100%;
  z-index: -1;
}

header .scrollDown {
  width: 100%;
  position: relative;
  text-align: center;
  z-index: 55;
  cursor: pointer;
  top: 110px;
  animation: moveing 0.7s linear alternate infinite;
}

header svg {
  width: 70px;
  height: 70px;
  fill: #fff;
}

@keyframes moveing {
  from {
    top: 110px;
  }

  to {
    top: 140px;
  }
}

